vue.ini = function (key, value) {
	if (arguments.length > one) return vue.ini.data [key] = value
	else if (key) if (lib.is.object (key)) vue.react (vue.ini.data, key)
	else return vue.ini.data [key]
	else return vue.ini.data
	}

