<script>
import vue, {the, lib, library} from "vue.min.js"
export default vue.component ({
	id: "router:redirect",
	attribute: ["url"],
	setup: function (prop) {
		lib.time.sleep (function () { vue.url.go (prop.url) })
		return vue.data ()
		},
	method: {
		slot () { return vue.slot (this).default },
		},
	})
</script>

<template id="router:redirect" type="component">
	<div v-if="slot ()">
		<slot slot/>
	</div>
</template>
