<script>
import vue, {lib, library} from "vue.min.js"
export default vue.component ({
	id: "media-query:progress",
	attribute: [],
	setup: function () {
		var aria = vue.reference ("off")
		var progress = vue.language ("loading") + " ..."
		if (null) progress = the.app.name
		vue.watch (vue.on.progress ("reference"), function (data) {
			if (vue.on.progress ()) aria.value = "on"
			else aria.value = "off"
			})
		return vue.data ({progress, aria})
		},
	method: {
		slot () { return vue.slot (this).default },
		},
	})
</script>

<template id="media-query:progress" type="component">
	<div id="media-query:progress" class="flex align-item justify-item view-port" style="background-color: white" v-bind:aria-data="aria">
		<slot v-if="slot ()"/>
		<i v-else>{{ progress }}</i>
	</div>
</template>
