<script>
import vue, {lib, library} from "vue.min.js"
export default vue.component ({
	id: "media-query:screen-splash",
	attribute: [],
	setup: function (prop) {
		var a_data = vue.reference ()
		vue.watch (vue.media.query.screen.splash.show.status, function (on) {
			if (on) a_data.value = "on"
			else a_data.value = null
			})
		return {vue, a_data}
		},
	method: {
		function () {},
		},
	})
</script>

<template id="media-query:screen-splash" type="component">
	<div id="media-query:screen-splash" class="view-port absolute flex-row" style="background-color: white" v-bind:aria-data="a_data">
		<div class="view-port flex flex-row align-item justify-item">
			<div>
				<img:logo src="192.png" width="192"/>
			</div>
		</div>
		<the:footer-trademark/>
	</div>
</template>
