<script>
import vue, {the, lib, library} from "vue.min.js"
export default vue.component ({
	id: "the:page/sales",
	attribute: [],
	setup: function (prop) {
		vue.model ("price/box", the.option ("price/box"))
		vue.model ("price/piece", the.option ("price/piece"))
		vue.model ("product:ultimate/unit", "box")
		vue.model ("product:kasturi/unit", "box")
		vue.model ("product:soft/unit", "box")
		vue.model ("product:baccarat/unit", "box")
		vue.model ("product:mix/unit", "box")
		return vue.data ()
		},
	method: {
		function () {},
		submit () {
			var model = {
				"price/box": vue.model ("price/box"),
				"price/piece": vue.model ("price/piece"),
				product: {
					ultimate: {quantity: vue.model ("product:ultimate/quantity"), unit: vue.model ("product:ultimate/unit")},
					kasturi: {quantity: vue.model ("product:kasturi/quantity"), unit: vue.model ("product:kasturi/unit")},
					soft: {quantity: vue.model ("product:soft/quantity"), unit: vue.model ("product:soft/unit")},
					baccarat: {quantity: vue.model ("product:baccarat/quantity"), unit: vue.model ("product:baccarat/unit")},
					mix: {quantity: vue.model ("product:mix/quantity"), unit: vue.model ("product:mix/unit")},
					},
				}
			var store = {id: "", name: vue.model ("store-name")}
			var transaction = {id: lib.fire.store.id (), reference: the.transaction.reference (), detail: []}
			var stamp = lib.time.stamp ()
			var item = [], index = zero, quantity = zero, box = zero, piece = zero, price = zero
			var product = {
				ultimate: {name: "ultimate", quantity: model.product.ultimate.quantity, unit: model.product.ultimate.unit},
				kasturi: {name: "kasturi", quantity: model.product.kasturi.quantity, unit: model.product.kasturi.unit},
				soft: {name: "soft", quantity: model.product.soft.quantity, unit: model.product.soft.unit},
				baccarat: {name: "baccarat", quantity: model.product.baccarat.quantity, unit: model.product.baccarat.unit},
				mix: {name: "mix", quantity: model.product.mix.quantity, unit: model.product.mix.unit},
				}
			for (var i in product) if (product [i].quantity) {
				item.push ({_id: "", name: product [i].name, unit: product [i].unit, quantity: {}})
				if (product [i].unit === "box") if (box = box + (item [index].quantity.box = product [i].quantity)) item [index].price = model ["price/box"]
				if (product [i].unit === "piece") if (piece = piece + (item [index].quantity.piece = product [i].quantity)) item [index].price = model ["price/piece"]
				price = price + (product [i].quantity * (product [i].unit === "box" ? model ["price/box"] : model ["price/piece"]))
				index ++
				}
			for (var i in item) {
				transaction.detail.push ({
					"$:id": lib.fire.store.id (),
					"$:identity": transaction.id,
					"$:reference": transaction.reference,
					product: {_id: item [i]._id, name: item [i].name.big ()},
					unit: item [i].unit,
					quantity: item [i].quantity [item [i].unit],
					price: item [i].price,
					total: (item [i].quantity [item [i].unit] * item [i].price),
					})
				}
			transaction.data = {
				"$:id": transaction.id,
				"$:reference": transaction.reference,
				"$:stamp": lib.date.stamp ("serial"),
				store,
				item, quantity: {box, piece}, price,
				type: "default", payment: {type: "default", status: ""},
				}
			if (item.length) {
				the.sale.insert (transaction.data)
				}
			var thermal = {print: the.print.format (transaction, store, transaction.detail)}
			if (true) if (vue.visitor.browser.model) lib.mobile.a.thermal.print (thermal.print)
			},
		},
	})
const {zero, one} = lib
</script>

<template id="the:page/sales" type="component">
	<div class="flex-grow padding">
		PER BOX <input:number model="price/box" class="input"/>
		<br break/><br break/>
		PER PIECE <input:number model="price/piece" class="input"/>
		<br break/><br break/>
		NAME <input:text model="store-name" class="input"/>
		<br break/><br break/>
		ULTIMATE <input:number model="product:ultimate/quantity" class="input"/>
			<select:option model="product:ultimate/unit" class="input">
				<option value="box">BOX</option>
				<option value="piece">PIECE</option>
			</select:option>
		<br break/><br break/>
		KASTURI <input:number model="product:kasturi/quantity" class="input"/>
			<select:option model="product:kasturi/unit" class="input">
				<option value="box">BOX</option>
				<option value="piece">PIECE</option>
			</select:option>
		<br break/><br break/>
		SOFT <input:number model="product:soft/quantity" class="input"/>
			<select:option model="product:soft/unit" class="input">
				<option value="box">BOX</option>
				<option value="piece">PIECE</option>
			</select:option>
		<br break/><br break/>
		BACCARAT <input:number model="product:baccarat/quantity" class="input"/>
			<select:option model="product:baccarat/unit" class="input">
				<option value="box">BOX</option>
				<option value="piece">PIECE</option>
			</select:option>
		<br break/><br break/>
		MIX <input:number model="product:mix/quantity" class="input"/>
			<select:option model="product:mix/unit" class="input">
				<option value="box">BOX</option>
				<option value="piece">PIECE</option>
			</select:option>
		<br break/><br break/>
		<button:material v-on:click="submit ()">SUBMIT</button:material>
	</div>
</template>
