the.account = function () {
	if (the.account.cache ()) {}
	else { the.account.cache.clear () }
	}

the.account.setup = function () {
	vue.on.progress.insert ("account")
	vue.fire.store.select ("account").catch ().then (function (data) {
		the.account.on.line ("check")
		vue.on.progress.delete ("account")
		vue.state (function (state) {
			if (data.length) {
				the.account (state.account = data)
				}
			})
		})
	}

the.account.cache = function (current) {
	the.account.cache.id = vue.state ("account").index_of (function (account) { return account.get ("$:id") === vue.session.get ("account") })
	the.account.cache.current = [vue.state ("account").select ("*", {"$:id": (the.account.id = vue.session.get ("account"))})]
	if (the.account.cache.current.get ()) {
		if (current) {
			vue.state (function (state) {
				for (var i in current) state.account [the.account.cache.id][i] = current [i]
				the.account.cache.current = [state.account [the.account.cache.id]]
				})
			}
		the.account.doc = {id: the.account.cache.current.get ("$:doc")}
		the.account.user = the.account.cache.current.get ("user")
		the.account.password = the.account.cache.current.get ("password")
		the.account.access.data = the.account.cache.current.get ("access")
		the.account.on.line (true)
		the.account.cache.ready (true)
		return true
		}
	}

the.account.cache.clear = function () {
	the.account.on.line (null)
	vue.session.delete ("account")
	}

the.account.cache.ready = function (cache) {
	if (cache === "reference") return the.account.cache.ready.status
	else if (arguments.length) return the.account.cache.ready.status.value = cache
	else return the.account.cache.ready.status.value
	}

the.account.cache.ready.status = vue.reference ()

the.account.register = function (data = {}) {
	var account = {
		"$:id": lib.fire.store.id (),
		"$:stamp": lib.date.stamp ("serial"),
		user: data.user,
		password: lib.hash.password (data.password),
		status: data.status,
		access: data.access,
		}
	if (data.doc) if (data.doc.id) if (account ["$:doc"] = data.doc.id) return vue.fire.store.insert ("account").doc (data.doc.id).data (account).catch (console.error)
	else return vue.fire.store.insert ("account").data (account).catch ()
	else return vue.fire.store.insert ("account").data (account).catch ()
	return this
	}

the.account.login = function (user, password) {
	var account, filter = function (account) {
		if (account.user === user) if (account.password === lib.hash.password (password)) if (account.status === "active") {
			return true
			}
		}
	if ((account = vue.state ("account").filter (filter)).length === one) {
		vue.session.set ({account: account.get ("$:id")})
		var data = {log: lib.object.merge (account.get ("log"), {in: lib.time.stamp ()})}
		the.account.cache (data)
		if (true) return vue.fire.store.update ("account").doc (account.get ("$:doc")).data (data).catch ()
		else return vue.fire.store.update ("account").current (account.get ()).doc (account.get ("$:doc")).data ({log: {in: lib.time.stamp ()}}).catch ()
		}
	else return false
	}

the.account.logout = function (context) {
	the.account.cache.clear ()
	var data = {log: lib.object.merge (the.account.cache.current.get ("log"), {out: lib.time.stamp ()})}
	vue.state (function (state) {
		state.account [the.account.cache.id].log.out = data.log.out
		the.account.cache.current = [state.account [the.account.cache.id]]
		})
	if (true) return vue.fire.store.update ("account").doc (the.account.doc.id).data (data).catch (console.error)
	else return vue.fire.store.update ("account").current (the.account.cache.current.get ()).doc (the.account.doc.id).data ({log: {out: lib.time.stamp ()}}).catch (console.error)
	}

the.account.access = function (access) {
	if (the.account.access.data.exist ("*")) return true
	else if (lib.is.array (access)) for (var i in access) if (the.account.access.data.exist (access [i])) return true
	return the.account.access.data.exist (access)
	}

the.account.access.data = []

the.account.on = function () {}

the.account.on.line = function (line) {
	if (line === "check") {
		if (the.account.id = vue.session.get ("account")) {
			the.account.on.line (true)
			vue.on.progress.delete ("account")
			}
		}
	else if (line === "reference") return the.account.on.line.status
	else if (arguments.length) return the.account.on.line.status.value = line
	else return the.account.on.line.status.value
	}

the.account.on.line.status = vue.reference ()

