the.sale = function () {}

the.sale.setup = function () {
	vue.fire.store.select ("sale").catch (console.error).then (function (data) {
		vue.state (function (state) {
			if (data.length) {
				data.fetch (function (sale) {
					state.sale.cache.id = sale.get ("$:doc")
					state.sale.cache.size [sale.get ("$:doc")] = (sale.get ("__") || [].length)
					if (sale.get ("__")) {
						for (var i in sale.get ("__")) {
							state.sale.list.push (sale.get ("__") [i])
							}
						if ((state.sale.cache.size [sale.get ("$:doc")] = sale.get ("__").length) < the.option ("cache:size")) {
							state.sale.cache.id = sale.get ("$:doc")
							state.sale.cache.list = sale.get ("__")
							}
						else delete state.sale.cache.id
						}
					})
				}
			})
		})
	}

the.sale.insert = function (data) {
	vue.state (function (state) {
		if (state.sale.cache.id) {
			state.sale.cache.size [state.sale.cache.id] ++
			if (state.sale.cache.size [state.sale.cache.id] < the.option ("cache:size")) {
				state.sale.cache.list.push (data)
				return vue.fire.store.update ("sale").doc (state.sale.cache.id).data ({__: state.sale.cache.list}).catch (console.error).then ()
				}
			else return the.sale.insert.query (state, data)
			}
		else return the.sale.insert.query (state, data)
		})
	}

the.sale.insert.query = function (state, data) {
	state.sale.cache.id = data ["$:stamp"].insert.string ()
	state.sale.cache.size [state.sale.cache.id] = one
	state.sale.cache.list = [data]
	return vue.fire.store.insert ("sale").doc (state.sale.cache.id).data ({__: state.sale.cache.list}).catch (console.error).then ()
	}

