the.transaction = function () {}

the.transaction.reference = function () {
	var reference = lib.hash.shuffle (lib.number.char)
	if (true) return [1, reference.substr (0, 3), reference.substr (3, 5)].join ("-")
	else return [reference.substr (0, 3), reference.substr (3, 5), reference.substr (8, 8)].join ("-")
	}

the.transaction.setup = function () {
	vue.fire.store.select ("transaction").catch ().then (function (data) {
		vue.state (function (state) {
			if (data.length) {
				for (var i in data) {
					if (data [i].xxx) {
						for (var x in data [i].xxx) {
							state.transaction.list.push (data [i].xxx [x])
							}
						if ((state.transaction.cache.size [data [i]._doc] = data [i].xxx.length) < the.option ("cache:size")) {
							state.transaction.cache.id = data [i]._doc
							state.transaction.cache.list = data [i].xxx
							}
						else delete state.transaction.cache.id
						}
					}
				}
			})
		})
	}

the.transaction.select = function () {
	return
	}

the.transaction.insert = function (data) {
	vue.state (function (state) {
		if (state.transaction.cache.id) {
			state.transaction.cache.size [state.transaction.cache.id] ++
			if (state.transaction.cache.size [state.transaction.cache.id] < the.option ("cache:size")) {
				state.transaction.cache.list.push (data)
				vue.fire.store.update ("transaction").doc (state.transaction.cache.id).data ({xxx: state.transaction.cache.list}).catch (console.error).then ()
				}
			else the.transaction.insert.query (state, data)
			}
		else the.transaction.insert.query (state, data)
		})
	}

the.transaction.insert.query = function (state, data) {
	state.transaction.cache.id = data ["$:stamp"].insert.string ()
	state.transaction.cache.size [state.transaction.cache.id] = one
	state.transaction.cache.list = [data]
	return vue.fire.store.insert ("transaction").doc (state.transaction.cache.id).data ({xxx: state.transaction.cache.list}).catch (console.error).then ()
	}

