the.print = function () {}
the.print.format = function (transaction, store, item) {
	var app_name = the.app.name
	var app_description = "Concentrated Fragrance"
	var app_slogan = "Satu Olesan Sejuta Kebaikan"
	var app_phone = the.app.phone
	var app_web_site = "www.iperfume.id"
	var print = []
	print.push ("[C]")
	print.push ("[C]" + app_name)
	print.push ("[C]" + app_description)
	print.push ("[C]")
	print.push ("[L]" + vue.date.format ("L, F D, Y"))
	print.push ("[L]" + vue.date.format ("J:I:S A"))
	print.push ("[C]")
	if (store) if (store.name) print.push ("[L]" + "STORE : " + store.name)
	print.push ("[L]" + "TRANSACTION ID : " + transaction.reference)
	print.push ("[C]")
	print.push ("[C]================================")
	print.push ("[L]VARIANT                      QTY")
	print.push ("[C]--------------------------------")
	var total = zero
	for (var i in item) {
		var name = item [i].product.name
		var quantity = (item [i].quantity || zero), q = lib.number.format (quantity, {separator: "."})
		var unit = item [i].unit
		var price = item [i].price
		if (unit) {
			var space = 32 - (name.length + q.length + unit.length + one)
			print.push ("[L]" + name + lib.char.space.repeat (space) + q + lib.char.space + unit.big ())
			}
		else {
			var space = 32 - (name.length + q.length)
			print.push ("[L]" + name + lib.char.space.repeat (space) + q)
			}
		total = total + (quantity * price)
		}
	total = lib.number.format (total, {separator: "."})
	var space = 32 - (total.length + 5)
	print.push ("[C]--------------------------------")
	print.push ("[L]TOTAL" + lib.char.space.repeat (space) + total)
	print.push ("[C]================================")
	print.push ("[C]")
	print.push ("[C]TERIMA KASIH")
	print.push ("[C]HOT LINE " + app_phone)
	print.push ("[C]" + app_web_site)
	print.push ("[C]")
	print = print.join ("\n")
	return print
	}

