<script>
import vue, {lib, library} from "vue.min.js"
export default vue.component ({
	id: "media-query:loading",
	attribute: [],
	setup: function () {
		var aria = vue.reference ("off")
		vue.watch (vue.on.loading ("reference"), function (data) {
			if (vue.on.loading ()) aria.value = "on"
			else aria.value = "off"
			})
		return {vue, aria}
		},
	})
</script>

<template id="media-query:loading" type="component">
	<div id="media-query:loading" class="x" style="background-color: gray" v-bind:aria-data="aria">
		{{ vue.on.loading.data }}
	</div>
</template>
