<script>
import vue, {the, lib, library} from "vue.min.js"
export default vue.component ({
	id: "the:nav",
	attribute: ["url", "icon", "option", "src", "width"],
	setup: function (prop) {
		var url = vue.reference (prop.url)
		var icon = vue.reference (prop.icon)
		var option = vue.reactive ({icon: {}})
		vue.react (option, prop.option)
		var menu = vue.reference ()
		var src = vue.reference (prop.src || "more")
		var width = vue.reference (option.width || "200px")
		var logout = function () {
			the.account.logout ().then (function () {
				vue.router.go ("account/login.html")
				})
			}
		if (src.value === "account") menu.value = [
			{name: "Profile", url: "account/profile.html", icon: "manage_accounts", type: "anchor"},
			{type: "separator"},
			{name: "Sign Out", url: "cpanel.html", click: logout, icon: "logout", type: "anchor"},
			]
		if (src.value === "notification") menu.value = [
			{name: "Notification", url: {name: "service/notification.html", query: {id: "service"}}, icon: "notifications_active", type: "anchor"},
			]
		if (src.value === "more") {
			menu.value = [
				{name: "Home", url: "cpanel.html", icon: "home_app_logo", type: "anchor"},
				{name: "Product", url: "cpanel.html", icon: "nutrition", type: "anchor"},
				{name: "Store", url: "store.html", icon: "home_pin", type: "anchor"},
				{name: "Location Map", url: "map.html", icon: "pin_drop", type: "anchor"},
				{type: "separator"},
				{name: "Point of Sale's", url: "/", icon: "insights", type: "anchor"},
				{type: "separator"},
				]
			}
		if (the.account.cache.ready ()) {
			if (src.value === "more") {
				if (the.account.access ("*")) {
					menu.value.push ({id: "", name: "", url: "/", icon: "", type: "anchor"})
					}
				if (the.account.access ("sales")) {
					menu.value.push ({id: "sell", name: "Sell", url: "cpanel.html", icon: "description", type: "anchor"})
					}
				}
			}
		vue.watch (the.account.cache.ready ("reference"), function () {
			if (the.account.cache.ready ()) {
				if (src.value === "more") {
					if (the.account.access ("*")) {
						if (menu.value.select ("*", {id: ""})) {}
						else menu.value.push ({id: "", name: "", url: "/", icon: "", type: "anchor"})
						}
					if (the.account.access ("sales")) {
						if (menu.value.select ("*", {id: "sell"})) {}
						else menu.value.push ({id: "sell", name: "Sell", url: "cpanel.html", icon: "description", type: "anchor"})
						}
					}
				}
			})
		return vue.data ({url, icon, option, src, menu, width})
		},
	method: {
		function () {},
		slot () { return vue.slot (this).default },
		click () { if (this.url) vue.router.go (this.url) },
		is_anchor (input) { return input === "anchor" },
		is_separator (input) { return input === "separator" },
		is_account (input) { return input === "account" },
		is_notification (input) { return input === "notification" },
		is_more (input) { return input === "more" },
		},
	})
</script>

<template id="the:nav" type="component">
	<div component="the:nav">
		<button:material v-if="is_account (src)" icon="person" accent="transparent" v-bind:option="{icon: {style: 'symbol'}}"/>
		<button:material v-else-if="is_notification (src)" icon="notifications" accent="transparent" v-bind:option="{icon: {style: 'symbol'}}"/>
		<button:material icon="more_vert" accent="transparent" v-bind:option="{icon: {style: 'symbol'}}" v-else/>
		<ul v-bind:class="('flex-row align-item border-radius absolute box-shadow').implode ('right')" v-bind:style="lib.object.to_style ({'min-width': width})">
			<li v-for="nav in menu">
				<a:link v-if="is_anchor (nav.type)" v-bind:url="nav.url" v-on:click="nav.click ? nav.click () : null">
					<icon:symbol v-if="nav.icon" v-bind:src="nav.icon" v-bind:type="option.icon.type" v-bind:weight="option.icon.weight" v-bind:class="option.icon.css"/>
					<span v-if="nav.name">{{ nav.name }}</span>
				</a:link>
				<div v-else-if="is_separator (nav.type)" class="separator"></div>
			</li>
		</ul>
	</div>
</template>

<style scoped>
[component="the:nav"] { position: relative; }
button:focus + ul { display: flex; }
ul { display: none; background-color: white; }
ul:hover { display: flex; }
ul li { width: 100%; color: rgb(var(--font-color)); }
ul li a { display: flex; align-items: center; gap: 10px; padding: 10px; color: rgb(var(--font-color)); }
ul li a:hover { background-color: rgba(var(--background-mono-color), 0.55); }
.separator { width: 100%; height: var(--pixel); background-color: rgb(var(--background-mono-color)); }
</style>
