<script>
import vue, {the, lib, library} from "vue.min.js"
export default vue.component ({
	id: "the:header",
	attribute: ["src"],
	setup: function (prop) {
		var src = vue.reference (prop.src || "default")
		return vue.data ({src})
		},
	method: {
		function () {},
		go__ () {
			if (the.account.on.line ()) vue.router.go ("cpanel.html")
			else vue.router.go ("/")
			},
		logout () {
			the.account.logout ().then (function () {
				vue.router.go ("account/login.html")
				})
			},
		is_default (input) { return input === "default" },
		},
	})
</script>

<template id="the:header" type="component">
	<div component="the:header">
		<div v-if="is_default (src)" class="flex align-item gap x fixed top" component="the:header-float">
			<div class="padding-left"></div>
			<div v-on:click="go__ ()">
				<img:logo src="48.png" width="32"/>
			</div>
			<div v-on:click="go__ ()">
				<b font="family:logo">{{ the.app.name }}</b>
			</div>
			<div class="flex-grow"></div>
			<div>
				<div v-if="the.account.on.line ()" class="flex align-item gap padding-right-medium">
					<the:nav src="account" v-bind:option="{icon: {type: 'line'}}"/>
					<the:nav src="notification" v-bind:option="{icon: {type: 'line'}}"/>
					<the:nav src="more" v-bind:option="{icon: {type: 'line'}}"/>
				</div>
				<div class="flex align-item gap-large padding-right-big" v-else>
					<a:link url="page:about">About</a:link>
					<a:link url="page:help">Help</a:link>
				</div>
			</div>
		</div>
	</div>
</template>

<style scoped>
[component="the:header"] { min-height: var(--nav-bar); }
[component="the:header-float"] { min-height: var(--nav-bar); border-bottom: 0px solid gray; background-color: white; }
</style>
