<script>
import vue, {lib, library} from "vue.min.js"
export default vue.component ({
	id: "media-query:line",
	attribute: [],
	setup: function () {
		var a_data = vue.reference ("on")
		vue.watch (vue.on.line, function (on) {
			if (on) a_data.value = "on"
			else a_data.value = "off"
			})
		return {vue, a_data}
		},
	method: {
		on () {
			return this.a_data === "on"
			}
		},
	})
</script>

<template id="media-query:line" type="component">
	<div v-if="on ()" id="media-query:line" v-bind:aria-data="a_data">
		<div id="media-query:line/section">
			on:line
		</div>
	</div>
	<div id="media-query:line" v-bind:aria-data="a_data" v-else>
		<div id="media-query:line/section">
			off:line
		</div>
	</div>
</template>
