import vue, {lib, library} from "vue.min.js"
var router = []
router.push ({path: "/", name: "/", component: () => import ("../../router/index.vue"), meta: {}})
router.push ({path: "/about", name: "page:about", component: () => import ("../../router/link/about.vue"), meta: {}})
router.push ({path: "/account/login", name: "account/login.html", component: () => import ("../../router/link/account/login.vue"), meta: {}})
router.push ({path: "/account/profile", name: "account/profile.html", component: () => import ("../../router/link/account/profile.vue"), meta: {}})
router.push ({path: "/cpanel", name: "cpanel.html", component: () => import ("../../router/link/cpanel/index.vue"), meta: {}})
router.push ({path: "/map", name: "map.html", component: () => import ("../../router/link/cpanel/map.vue"), meta: {}})
router.push ({path: "/store", name: "store.html", component: () => import ("../../router/link/cpanel/store.vue"), meta: {}})
router.push ({path: "/help", name: "page:help", component: () => import ("../../router/link/help.vue"), meta: {}})
router.push ({path: "/notification/:id", name: "service/notification.html", component: () => import ("../../router/link/service/notification.vue"), meta: {}})
router.push ({path: lib.router.try.catch, name: "*", component: () => import ("../../router/slot/index.vue"), meta: {}})
export default router = lib.router.create ({history: lib.router.history (vue.request.url), [("route").concat ("s")]: router})